import * as React from "react"
import { ExternalLink, FileText, Table, FileCheck } from "lucide-react"

const embedConfigs = {
    "google-sheet": {
        icon: Table,
        label: "Google Sheets",
        color: "#0F9D58",
        getEmbedUrl: (url) => {
            if (url.includes('/edit')) {
                return url.replace('/edit', '/preview')
            }
            return url + '/preview'
        }
    },
    "google-doc": {
        icon: FileText,
        label: "Google Docs",
        color: "#4285F4",
        getEmbedUrl: (url) => {
            if (url.includes('/edit')) {
                return url.replace('/edit', '/preview')
            }
            return url + '/preview'
        }
    },
    "google-form": {
        icon: FileCheck,
        label: "Google Form Template",
        color: "#673AB7",
        getEmbedUrl: (url) => {
            // For template preview URLs, keep as-is for embedding
            return url
        }
    },
    "pdf": {
        icon: FileText,
        label: "PDF Document",
        color: "#EA4335",
        getEmbedUrl: (url) => url
    },
    "canva": {
        icon: FileText,
        label: "Canva Template",
        color: "#00C4CC",
        getEmbedUrl: (url) => url
    }
}

const EmbeddedDocCard = ({ url, embedType, title, region }) => {
    const [isLoading, setIsLoading] = React.useState(true)

    const config = embedConfigs[embedType] || embedConfigs.pdf
    const IconComponent = config.icon
    const embedUrl = url ? config.getEmbedUrl(url) : null

    const handleIframeLoad = () => {
        setIsLoading(false)
    }

    // If no URL, show placeholder
    if (!url) {
        return (
            <div className="embedded-doc-card rounded-xl overflow-hidden border border-[var(--color-editorial-border)]">
                <div 
                    className="p-4 flex items-center gap-3"
                    style={{ 
                        backgroundColor: `${config.color}10`,
                        borderBottom: `1px solid var(--color-editorial-border)`
                    }}
                >
                    <div 
                        className="w-10 h-10 rounded-lg flex items-center justify-center"
                        style={{ backgroundColor: `${config.color}20` }}
                    >
                        <IconComponent className="w-5 h-5" style={{ color: config.color }} />
                    </div>
                    <div>
                        <p 
                            className="text-[var(--color-editorial-black)] font-semibold"
                            style={{ fontSize: "var(--text-sm)" }}
                        >
                            {config.label}
                        </p>
                        <p 
                            className="text-[var(--color-editorial-grey)]"
                            style={{ fontSize: "var(--text-xs)" }}
                        >
                            Coming Soon
                        </p>
                    </div>
                </div>
                <div 
                    className="h-[400px] bg-[var(--color-editorial-light-bg)] flex items-center justify-center"
                >
                    <div className="text-center p-6">
                        <IconComponent 
                            className="w-12 h-12 mx-auto mb-3 opacity-30" 
                            style={{ color: config.color }} 
                        />
                        <p 
                            className="text-[var(--color-editorial-grey)]"
                            style={{ fontSize: "var(--text-sm)" }}
                        >
                            This resource is being prepared
                        </p>
                    </div>
                </div>
            </div>
        )
    }

    // For Google Forms - styled like Tools section cards
    if (embedType === 'google-form') {
        // Region-specific card content
        const regionContent = {
            india: {
                title: "ðŸ“‹ Form C Guest Data Collector",
                description: "Pre-built form with <strong>exact fields required by FRRO</strong> for foreign guest registration in India. Collects passport details, visa info, and arrival stamps.",
                features: ["FRRO Compliant", "Auto-save Responses", "Mobile Friendly"]
            },
            thailand: {
                title: "ðŸ“‹ TM30 Guest Data Collector",
                description: "Pre-built form with <strong>exact fields required for TM30</strong> notification in Thailand. Includes TM.6 Departure Card fields and 24-hour reporting support.",
                features: ["TM30 Compliant", "Auto-save Responses", "Mobile Friendly"]
            },
            vietnam: {
                title: "ðŸ“‹ NA17 Guest Data Collector",
                description: "Pre-built form with <strong>exact fields for NA17</strong> Temporary Residence Declaration in Vietnam. Tracks visa expiry dates and immigration requirements.",
                features: ["NA17 Compliant", "Auto-save Responses", "Mobile Friendly"]
            }
        }

        const content = regionContent[region] || regionContent.india

        return (
            <a
                href={url}
                target="_blank"
                rel="noopener noreferrer"
                className="embedded-doc-card block bg-white rounded-lg overflow-hidden transition-all duration-300 hover:shadow-lg cursor-pointer group"
            >
                {/* Card Body */}
                <div className="p-6 rounded-lg border border-gray-200">
                    {/* Icon and Badge Row */}
                    <div className="flex items-start justify-between mb-4">
                        <div 
                            className="w-10 h-10 rounded-lg flex items-center justify-center"
                            style={{ backgroundColor: '#673AB715' }}
                        >
                            <FileCheck className="w-5 h-5" style={{ color: '#673AB7' }} />
                        </div>
                        <span 
                            className="px-3 py-1 rounded-full text-white text-xs font-semibold"
                            style={{ backgroundColor: 'var(--color-editorial-accent)' }}
                        >
                            Google Form
                        </span>
                    </div>
                    
                    {/* Title */}
                    <h3 
                        className="text-gray-900 mb-3"
                        style={{ fontSize: 'var(--text-lg)', fontWeight: 800 }}
                    >
                        {content.title}
                    </h3>
                    
                    {/* Description */}
                    <p 
                        className="text-gray-600 mb-5" 
                        style={{ fontSize: 'var(--text-sm)', lineHeight: 1.7 }}
                        dangerouslySetInnerHTML={{ __html: content.description }}
                    />

                    {/* Features Row */}
                    <div className="flex flex-wrap gap-2 mb-6">
                        {content.features.map((feature, index) => (
                            <span 
                                key={index}
                                className="inline-flex items-center gap-1 px-3 py-1.5 rounded-full bg-gray-100 text-gray-700 text-xs font-medium"
                            >
                                âœ“ {feature}
                            </span>
                        ))}
                    </div>

                    {/* CTA Button - Green with white text */}
                    <div 
                        className="inline-flex items-center gap-2 px-6 py-3 rounded-lg text-white font-semibold transition-all group-hover:opacity-90 whitespace-nowrap"
                        style={{ 
                            backgroundColor: 'var(--color-editorial-accent)',
                            fontSize: 'var(--text-sm)'
                        }}
                    >
                        Use This Template <ExternalLink className="w-4 h-4 inline-block" />
                    </div>
                </div>

                {/* Bottom hint */}
                <div className="px-6 py-3 bg-gray-50">
                    <span className="text-gray-500 text-xs">
                        Opens in Google Forms â†’ Click "Use Template" to create your copy
                    </span>
                </div>
            </a>
        )
    }

    // For other types (sheets, docs, etc.)
    return (
        <div className="embedded-doc-card rounded-xl overflow-hidden border border-[var(--color-editorial-border)] shadow-sm">
            {/* Header Bar */}
            <div 
                className="p-4 flex items-center justify-between"
                style={{ 
                    backgroundColor: `${config.color}10`,
                    borderBottom: `1px solid var(--color-editorial-border)`
                }}
            >
                <div className="flex items-center gap-3">
                    <div 
                        className="w-10 h-10 rounded-lg flex items-center justify-center"
                        style={{ backgroundColor: `${config.color}20` }}
                    >
                        <IconComponent className="w-5 h-5" style={{ color: config.color }} />
                    </div>
                    <div>
                        <p 
                            className="text-[var(--color-editorial-black)] font-semibold"
                            style={{ fontSize: "var(--text-sm)" }}
                        >
                            {title || config.label}
                        </p>
                        <p 
                            className="text-[var(--color-editorial-grey)]"
                            style={{ fontSize: "var(--text-xs)" }}
                        >
                            {config.label}
                        </p>
                    </div>
                </div>
                <a
                    href={url}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="inline-flex items-center gap-2 px-4 py-2 rounded-lg font-semibold text-white transition-all duration-200 hover:opacity-90"
                    style={{ 
                        fontSize: "var(--text-xs)",
                        backgroundColor: config.color
                    }}
                >
                    Open <ExternalLink className="w-3 h-3" />
                </a>
            </div>

            {/* Embed Area */}
            <div className="relative" style={{ height: '400px' }}>
                {/* Loading State */}
                {isLoading && (
                    <div className="absolute inset-0 flex items-center justify-center bg-[var(--color-editorial-light-bg)]">
                        <div className="flex flex-col items-center gap-3">
                            <div 
                                className="w-8 h-8 border-2 border-t-transparent rounded-full animate-spin"
                                style={{ borderColor: `${config.color}40`, borderTopColor: 'transparent' }}
                            />
                            <span 
                                className="text-[var(--color-editorial-grey)]"
                                style={{ fontSize: "var(--text-xs)" }}
                            >
                                Loading preview...
                            </span>
                        </div>
                    </div>
                )}

                {/* Iframe embed for sheets/docs */}
                {embedType !== 'pdf' && embedType !== 'canva' && embedUrl && (
                    <iframe
                        src={embedUrl}
                        title={title || config.label}
                        className="w-full h-full border-0"
                        onLoad={handleIframeLoad}
                        style={{ opacity: isLoading ? 0 : 1 }}
                    />
                )}

                {/* For PDFs, show a static preview card */}
                {embedType === 'pdf' && (
                    <div className="absolute inset-0 flex items-center justify-center bg-[var(--color-editorial-light-bg)]">
                        <div className="text-center p-6">
                            <FileText 
                                className="w-16 h-16 mx-auto mb-4" 
                                style={{ color: config.color }} 
                            />
                            <p 
                                className="text-[var(--color-editorial-black)] font-semibold mb-2"
                                style={{ fontSize: "var(--text-base)" }}
                            >
                                {title || "PDF Document"}
                            </p>
                            <p 
                                className="text-[var(--color-editorial-grey)] mb-4"
                                style={{ fontSize: "var(--text-sm)" }}
                            >
                                Click to download or view
                            </p>
                            <a
                                href={url}
                                target="_blank"
                                rel="noopener noreferrer"
                                className="inline-flex items-center gap-2 px-6 py-3 rounded-lg font-semibold text-white"
                                style={{ 
                                    fontSize: "var(--text-sm)",
                                    backgroundColor: config.color
                                }}
                            >
                                Download PDF <ExternalLink className="w-4 h-4" />
                            </a>
                        </div>
                    </div>
                )}

                {/* For Canva, show a preview card */}
                {embedType === 'canva' && (
                    <div className="absolute inset-0 flex items-center justify-center bg-[var(--color-editorial-light-bg)]">
                        <div className="text-center p-6">
                            <div 
                                className="w-16 h-16 mx-auto mb-4 rounded-full flex items-center justify-center"
                                style={{ backgroundColor: `${config.color}20` }}
                            >
                                <span className="text-3xl">ðŸŽ¨</span>
                            </div>
                            <p 
                                className="text-[var(--color-editorial-black)] font-semibold mb-2"
                                style={{ fontSize: "var(--text-base)" }}
                            >
                                {title || "Canva Template"}
                            </p>
                            <p 
                                className="text-[var(--color-editorial-grey)] mb-4"
                                style={{ fontSize: "var(--text-sm)" }}
                            >
                                Editable design template
                            </p>
                            <a
                                href={url}
                                target="_blank"
                                rel="noopener noreferrer"
                                className="inline-flex items-center gap-2 px-6 py-3 rounded-lg font-semibold text-white"
                                style={{ 
                                    fontSize: "var(--text-sm)",
                                    backgroundColor: config.color
                                }}
                            >
                                Open in Canva <ExternalLink className="w-4 h-4" />
                            </a>
                        </div>
                    </div>
                )}
            </div>
        </div>
    )
}

export default EmbeddedDocCard
