import * as React from "react"

const regions = [
    { key: "india", label: "India", flag: "ðŸ‡®ðŸ‡³" },
    { key: "thailand", label: "Thailand", flag: "ðŸ‡¹ðŸ‡­" },
    { key: "vietnam", label: "Vietnam", flag: "ðŸ‡»ðŸ‡³" },
]

const RegionTabs = ({ activeRegion, onRegionChange, children }) => {
    return (
        <div className="region-tabs">
            {/* Tab Headers */}
            <div 
                className="flex border-b border-[var(--color-editorial-border)] mb-6"
                role="tablist"
                aria-label="Region selection"
            >
                {regions.map((region) => (
                    <button
                        key={region.key}
                        role="tab"
                        aria-selected={activeRegion === region.key}
                        aria-controls={`panel-${region.key}`}
                        id={`tab-${region.key}`}
                        onClick={() => onRegionChange(region.key)}
                        className={`
                            flex items-center gap-2 px-6 py-4 font-semibold transition-all duration-200
                            border-b-2 -mb-[2px]
                            ${activeRegion === region.key 
                                ? 'border-[var(--color-editorial-accent)] text-[var(--color-editorial-accent)]' 
                                : 'border-transparent text-[var(--color-editorial-grey)] hover:text-[var(--color-editorial-black)] hover:border-[var(--color-editorial-border)]'
                            }
                        `}
                        style={{ fontSize: "var(--text-sm)" }}
                    >
                        <span className="text-xl">{region.flag}</span>
                        <span>{region.label}</span>
                    </button>
                ))}
            </div>

            {/* Tab Content */}
            <div 
                role="tabpanel"
                id={`panel-${activeRegion}`}
                aria-labelledby={`tab-${activeRegion}`}
                className="region-tab-content"
            >
                {children}
            </div>
        </div>
    )
}

// Sub-component for region-specific content display
