import * as React from "react"
import { Link } from "gatsby"
import Layout from "../components/layout/Layout"
import Seo from "../components/seo/Seo"
import RegionTabs, { RegionContent } from "../components/resources/RegionTabs"
import EmbeddedDocCard from "../components/resources/EmbeddedDocCard"
import { ArrowLeft, AlertTriangle, CheckCircle, FileText } from "lucide-react"

const categoryMeta = {
    "operational-templates": {
        title: "Operational Templates",
        color: "#10b981",
        backLink: "/resources/operational-templates/",
    },
    "financial-tools": {
        title: "Financial Tools", 
        color: "#f59e0b",
        backLink: "/resources/financial-tools/",
    },
    "guest-experience": {
        title: "Guest Experience Assets",
        color: "#8b5cf6",
        backLink: "/resources/guest-experience/",
    },
    "deep-dives": {
        title: "Authority Deep Dives",
        color: "#3b82f6",
        backLink: "/resources/deep-dives/",
    },
}

const ResourceTemplate = ({ pageContext }) => {
    const { resource } = pageContext
    const [activeRegion, setActiveRegion] = React.useState("india")

    const category = categoryMeta[resource.category] || categoryMeta["operational-templates"]
    const currentRegionData = resource.regions?.[activeRegion] || {}

    return (
        <Layout>
            <div className="max-w-[900px] mx-auto px-4 py-12">
                {/* Breadcrumb */}
                <nav className="mb-8">
                    <div className="flex items-center gap-2 text-[var(--color-editorial-grey)]" style={{ fontSize: "var(--text-sm)" }}>
                        <Link 
                            to="/resources/"
                            className="hover:text-[var(--color-editorial-accent)] transition-colors"
                        >
                            Resources
                        </Link>
                        <span>/</span>
                        <Link 
                            to={category.backLink}
                            className="hover:text-[var(--color-editorial-accent)] transition-colors"
                        >
                            {category.title}
                        </Link>
                    </div>
                </nav>

                {/* Back link */}
                <Link 
                    to={category.backLink}
                    className="inline-flex items-center gap-2 text-[var(--color-editorial-grey)] hover:text-[var(--color-editorial-accent)] transition-colors mb-8"
                    style={{ fontSize: "var(--text-sm)" }}
                >
                    <ArrowLeft className="w-4 h-4" />
                    Back to {category.title}
                </Link>

                {/* Header */}
                <header className="mb-10">
                    {/* Format badge */}
                    <div className="mb-4">
                        <span
                            className="inline-block px-4 py-1.5 rounded-full text-white font-medium"
                            style={{ 
                                fontSize: "var(--text-xs)", 
                                backgroundColor: category.color 
                            }}
                        >
                            {resource.format}
                        </span>
                    </div>

                    <h1
                        className="mb-4 text-[var(--color-editorial-black)]"
                        style={{ fontSize: "clamp(1.75rem, 4vw, 2.5rem)", fontWeight: 700, lineHeight: 1.2 }}
                    >
                        {resource.title}
                    </h1>

                    <p
                        className="text-[var(--color-editorial-grey)]"
                        style={{ fontSize: "var(--text-lg)", lineHeight: "1.7" }}
                    >
                        {resource.description}
                    </p>
                </header>

                {/* Pain Point Section */}
                {resource.painPoint && (
                    <div 
                        className="mb-10 p-6 rounded-xl border-l-4"
                        style={{ 
                            backgroundColor: "#fef3c720",
                            borderLeftColor: "#f59e0b"
                        }}
                    >
                        <div className="flex items-start gap-3">
                            <AlertTriangle className="w-5 h-5 mt-0.5 flex-shrink-0" style={{ color: "#f59e0b" }} />
                            <div>
                                <h3 
                                    className="text-[var(--color-editorial-black)] mb-2 font-semibold"
                                    style={{ fontSize: "var(--text-base)" }}
                                >
                                    The Problem This Solves
                                </h3>
                                <p 
                                    className="text-[var(--color-editorial-grey)]"
                                    style={{ fontSize: "var(--text-sm)", lineHeight: "1.6" }}
                                >
                                    {resource.painPoint}
                                </p>
                            </div>
                        </div>
                    </div>
                )}

                {/* Key Features */}
                {resource.keyFeatures && resource.keyFeatures.length > 0 && (
                    <div className="mb-10">
                        <h2 
                            className="text-[var(--color-editorial-black)] mb-6"
                            style={{ fontSize: "var(--text-xl)", fontWeight: 600 }}
                        >
                            Key Features
                        </h2>
                        <div className="space-y-4">
                            {resource.keyFeatures.map((feature, index) => (
                                <div 
                                    key={index}
                                    className="flex items-start gap-3 p-4 bg-[var(--color-editorial-light-bg)] rounded-lg"
                                >
                                    <CheckCircle 
                                        className="w-5 h-5 mt-0.5 flex-shrink-0" 
                                        style={{ color: category.color }} 
                                    />
                                    <p 
                                        className="text-[var(--color-editorial-black)]"
                                        style={{ fontSize: "var(--text-sm)", lineHeight: "1.6" }}
                                    >
                                        {feature}
                                    </p>
                                </div>
                            ))}
                        </div>
                    </div>
                )}

                {/* Region Tabs Section */}
                <div className="mb-10 pt-10">
                    <h2 
                        className="text-[var(--color-editorial-black)] mb-6"
                        style={{ fontSize: "var(--text-xl)", fontWeight: 600 }}
                    >
                        Region-Specific Versions
                    </h2>
                    
                    <RegionTabs 
                        activeRegion={activeRegion} 
                        onRegionChange={setActiveRegion}
                    >
                        <RegionContent 
                            region={activeRegion}
                            specifics={currentRegionData.specifics}
                            downloadUrl={currentRegionData.downloadUrl}
                            embedType={currentRegionData.embedType}
                        />
                    </RegionTabs>

                    {/* Embedded Document Card - inside the border */}
                    {currentRegionData.downloadUrl && (
                        <div className="mt-6">
                            <EmbeddedDocCard 
                                url={currentRegionData.downloadUrl}
                                embedType={currentRegionData.embedType}
                                title={resource.title}
                                region={activeRegion}
                            />
                        </div>
                    )}
                </div>
            </div>
        </Layout>
    )
}

export const Head = ({ pageContext }) => {
    const { resource } = pageContext
    return (
        <Seo
            title={`${resource.title} | Host Resources`}
            description={resource.description}
        />
    )
}

export default ResourceTemplate
